	module.exports = {"data":{"nodePost":{"title":"In The Garage: Joyce Manor Share The Scenes And Stories Behind Million Dollars To Kill Me","link":"https://www.stereogum.com/featured/joyce-manor-million-dollars-to-kill-me-interview/","body":{"value":"<blockquote>This January, they’re co-headlining the 3,700-cap Hollywood Palladium with Jeff Rosenstock, who they consider a fellow “last man standing” in their scene and the man responsible for Joyce Manor’s first paid gig in 2010. “As we were leaving, he asked ‘You guys get paid?'” Johnson recalls. “We’re like, it’s cool, we’re from around here, it’s fine. And he’s like no, no, no,” handing the band $100 out of his pocket.</blockquote>\r\n<p>Great profile in general, but this moment between two of my favorite bands stuck with me. Amazing to see Rosenstock give them what I'd imagine to be a non-trivial amount of money at the time just because he feels strongly that people should be compensated for their work. And I love that this impulsive moment also sticks with Joyce Manor eight years later as co-headliners. While I've never just handed someone $100, I can only hope that back in my improv days or now in the open source community I've had some small moments that have stuck with folks.</p>"},"relationships":{"image":null},"created":1535467148}},"pathContext":{"slug":"/link/in-the-garage:-joyce-manor-share-the-scenes-and-stories-behind-million-dollars-to-kill-me/"}}

